.card {
  height: 170px;
  width: 190px;
  margin: 8px 25px;
  border: 5px solid #f1f1f1;
  border-radius: 3px;
  box-shadow: 0 3px 6px rgba(0,0,0,.16), 0 3px 6px rgba(0,0,0,.23);
  cursor: pointer;
}
.card > .img-container { 
  height: 80%;
  margin-top: auto;
  margin-left: auto;
  overflow: hidden;
  text-align: center;  
}
.card > .img-container > img {
  margin-left: auto;
  margin-right: auto;
  width: 80% ;
  height: 80%;
}
.wrapper{display: flex;
  flex-flow: row wrap;
}
.align-baseline{
  margin-right: 70px;
}
h2{
  margin-left:70px;
}